.filter {

  &__section {
    margin: 0 0 50px 0;
    &:last-child {
      margin: 0; }
    #price-from,#price-to {
      max-width: 70px;
      margin: 0 30px 0 0; }
    #power {
      color: #D11586;
      font-weight: 500; } }

  &__title {
    color: #0066BA;
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    margin: 0 0 25px; }

  &__btn {
    display: inline-block;
    + .input-field {
      float: right; } }


  &__main-title {
    margin: 0 0 35px 0;
    font-size: 26px;
    line-height: 30px;
    &:before {
      background-color: #D11586; }
    @include breakpoint(md) {
      display: none; } } }


.show-filter {
  position: relative;
  display: flex;
  padding: 0 12px;
  background-color: #7EB5F9;
  font-size: 13px;
  line-height: 15px;
  color: #ffffff;
  width: fit-content;
  margin: 0 0 20px 0;
  z-index: 3;
  @include breakpoint(md) {
    position: absolute;
    left: calc(100% + 20px);
    top: -8px;
    margin: 0;
    z-index: 2; }
  &:before {
    content: '';
    height: 7px;
    width: 25px;
    background: url("../img/filter-show-top.svg") no-repeat;
    position: absolute;
    top: -7px;
    left: 30px;
    @include breakpoint(md) {
      height: 33px;
      width: 6px;
      top: 0;
      bottom: 0;
      left: -6px;
      right: unset;
      background: url("../img/filter-show-left.svg") no-repeat; } }
  &__text {
    display: flex;
    padding: 9px 10px 9px 0;
    border-right: 1px solid rgba(255, 255, 255, 0.15);
    span {
      margin: 0 5px 0 5px;
      font-weight: 700; } }
  &__link {
    display: flex;
    align-items: center;
    padding: 0 0 0 10px;
    font-weight: 700;
    color: #ffffff;
    text-decoration: underline; }
  &_right {
    margin: 0 0 20px auto;
    @include breakpoint(md) {
      margin: 0; }
    &:before {
      left: unset;
      right: 50px; } } }
